import Vue from 'vue';
import Vuex from 'vuex'
import DashboardPlugin from './plugins/dashboard-plugin';
import App from './App.vue';
// router setup
import router from './routes/router';
// store 
import store from './store/index'
import bus from './bus'
import query from "@/services/query";

// plugin setup
Vue.use(DashboardPlugin);

import tools from "./util/tkUtils";
Vue.use(tools);

import VueTouch from 'vue-touch'
Vue.use(VueTouch, { name: 'v-touch' })

import firebase from "./firebaseConfig";
import "firebase/auth"
Vue.use(firebase)

import * as fb from 'firebase/app'
Vue.use(fb)

import md5 from 'js-md5';
Vue.prototype.$md5 = md5;

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

import axios from 'axios'
Vue.prototype.$axios = axios;

/* eslint-disable no-new */
const app = new Vue({
  el: '#app',
  render: h => h(App),
  store,
  router
});

firebase.auth().onAuthStateChanged(async (user) => {
  console.log('监听到登录信息变更')
  let router = app.$router
  let uid = ""
  if (user) {
    localStorage.setItem('auth_user', JSON.stringify(user))
    if (router.history) {
      if (router.history.current) {
        if (router.history.current.name !== 'Login') {
          return
        }
      }
    }
    uid = user.uid

    let management = await query.getAccountManagement();
    let accounts = management.accounts;
    let accountType = management.accountType
    let account = null
    let isConsoleUser = false
    let permissions = []

    accounts.some(item => {
      if (item.userId == uid) {
        isConsoleUser = true
        account = item
        return
      }
    })

    if (isConsoleUser) {
      await accountType.some(item => {
        if (item.id == account.typeId) {
          permissions = item.permissions
        }
      })
      if (permissions.indexOf("0") > -1) {
        router.push({ name: "accountManagement" })
      } else if (permissions.indexOf("1") > -1) {
        router.push({ name: "log" })
      } else if (permissions.indexOf("5") > -1) {
        window.location = "https://tunekey.app/report/administration?cid=" + account.userId;
      } else {
        router.push({ name: "noOtherPermission" })
      }
    } else {
      router.push({ name: "Dashboard" })
    }
  } else {
    router.push({ name: "Login" })
  }
})

function redirect(routeName) {
  app.$router.push({ name: routeName })
}


