<script>
/* eslint-disable no-new */
import bus from "@/bus.js";
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";
import commons from "@/util/commons";
import query from "@/services/query";

function hasElement(className) {
  return document.getElementsByClassName(className).length > 0;
}

function initScrollbar(className) {
  if (hasElement(className)) {
    new PerfectScrollbar(`.${className}`);
  } else {
    // try to init it later in case this component is loaded async
    setTimeout(() => {
      initScrollbar(className);
    }, 100);
  }
}

import { SlideXRightTransition } from "vue2-transitions";

export default {
  data() {
    return {
      ready: false,
      showPage: false,
      // ready: true,
      userId: "", // 用户 id
      name: "Sophie Snow", //  用户名
      avatar:
        "https://demos.creative-tim.com/vue-argon-dashboard-pro//img/theme/team-4.jpg", // 用户头像
      showProfile: false,
      logo: "",
      studioId: "",
      isConsoleUser: false,
      isOriginalUser: false,
      accountManagement: [],
      permissions: [],

      tokenTimer: null,
    };
  },
  components: {
    SlideXRightTransition,
    SidebarItem: () => import("@/tkViews/components/SidebarPlugin/SidebarItem"),
    FullLoading: () => import("../components/layout/FullLoading.vue"),
    MyProfile: () => import("@/tkViews/components/Modal/MyProfile.vue"),
  },
  watch: {
    $route(to, from) {
      console.log("watch");
      this.initRoute();
    },
  },
  methods: {
    initRoute() {
      let to = this.$route;
      if (!this.isConsoleUser || !this.isOriginalUser) {
        if (this.isConsoleUser) {
          if (
            to.name !== "DynamicMarketLink" &&
            to.name !== "accountManagement" &&
            to.name !== "log" &&
            to.name !== "consoleUser" &&
            to.name !== "shakeToReport" &&
            to.name !== "screenshot" &&
            to.name !== "noOtherPermission"
          ) {
            if (this.permissions.indexOf("0") > -1) {
              this.$router.push({ name: "accountManagement" });
            } else if (this.permissions.indexOf("1") > -1) {
              this.$router.push({ name: "log" });
            } else if (this.permissions.indexOf("5") > -1) {
              window.location =
                "https://tunekey.app/report/administration?cid=" +
                account.userId;
            } else {
              this.$router.push({ name: "noOtherPermission" });
            }
          }
        }

        if (this.isOriginalUser) {
          if (
            to.name == "DynamicMarketLink" ||
            to.name == "accountManagement" ||
            to.name == "log" ||
            to.name == "consoleUser" ||
            to.name == "shakeToReport" ||
            to.name == "screenshot" ||
            to.name == "noOtherPermission"
          ) {
            this.$router.push({ name: "Dashboard" });
          }
        }
      }
      this.showPage = true;
      console.log("show page");
    },
    getUserConsoleUrl() {
      window.open(
        "https://tunekey.app/console/administration?cid=" + this.userId
      );
    },
    getScreenshotUrl() {
      window.open(
        "https://tunekey.app/screenshot/administration?cid=" + this.userId
      );
    },
    getShakeToReportUrl() {
      window.open(
        "https://tunekey.app/report/administration?cid=" + this.userId
      );
    },
    initScrollbar() {
      let isWindows = navigator.platform.startsWith("Win");
      if (isWindows) {
        initScrollbar("sidenav");
      }
    },
    logout() {
      let self = this;
      console.log("click log out");
      // if (this.isOriginalUser) {
      bus.$emit("showLoading");
      this.$firebase
        .auth()
        .signOut()
        .then(async function () {
          console.log("Sign-out successful.");
          let token = {};
          token[self.userId] = "";
          await query.updateAccountIdToken(token);

          localStorage.clear();
          self.$router.push({ name: "Login" });
          bus.$emit("hideLoading");
        })
        .catch(function (error) {
          console.log("Sign-out failed: ", error);
        });
      // } else {
      //   localStorage.clear();
      //   this.$router.push({ name: "Login" });
      // }
    },
    async generateToken() {
      let userId = this.userId;
      this.$firebase
        .auth()
        .currentUser.getIdToken(true)
        .then(async (idToken) => {
          let token = {};
          token[userId] = idToken;
          await query.updateAccountIdToken(token);
        });
    },
  },
  async mounted() {
    this.initScrollbar();
    bus.$on("showLoading", () => {
      this.ready = false;
    });

    bus.$on("hideLoading", () => {
      this.ready = true;
    });

    bus.$on("showProfile", () => {
      this.showProfile = true;
    });
  },
  async created() {
    this.accountManagement = await query.getAccountManagement();
    let user = localStorage.getItem("auth_user");
    let uid = "";
    console.log("dashboard user: ", JSON.parse(user));

    if (this.$tools.isNull(user)) {
      this.$router.push({ name: "Login" });
    } else {
      user = JSON.parse(user);
      uid = user.uid;
      let account = null;

      this.logo = "";
      this.userId = "";
      this.studioId = "";
      this.isConsoleUser = false;
      let accountType = this.accountManagement.accountType;
      let accounts = this.accountManagement.accounts;

      accounts.some((item) => {
        if (item.userId == uid) {
          account = item;
          this.isConsoleUser = true;
          this.name = item.name;
          this.userId = item.userId;
          return;
        }
      });

      console.log(this.name);

      if (this.isConsoleUser) {
        accountType.some((item) => {
          if (item.id == account.typeId) {
            this.permissions = item.permissions;
            return;
          }
        });

        this.generateToken();
      }

      if (!this.$tools.isNull(user)) {
        let teacherInfo = await query.getTeacherInfoById(user.uid);
        if (!this.$tools.isNull(teacherInfo)) {
          let userInfo = await query.getUserInfoByUserId(teacherInfo.userId);
          this.logo = commons.studioLogoPath(teacherInfo.studioId);
          this.userId = teacherInfo.userId;
          this.studioId = teacherInfo.studioId;
          this.name = userInfo.name;
          this.isOriginalUser = true;
        }
      }
      this.ready = true;
      this.initRoute();
    }
  },
};
</script>
