/**
 * Snowflake to get random id
 */
let time = 1563517787261;
let Snowflake = (function () {
    function Snowflake(_workerId, _dataCenterId, _sequence) {
        this.twepoch = time;
        this.workerIdBits = 2;
        this.dataCenterIdBits = 2;
        this.maxWrokerId = -1 ^ (-1 << this.workerIdBits); // value：31
        this.maxDataCenterId = -1 ^ (-1 << this.dataCenterIdBits); // value：31
        this.sequenceBits = 12;
        this.workerIdShift = this.sequenceBits; // value：12
        this.dataCenterIdShift = this.sequenceBits + this.workerIdBits; // value：17
        this.timestampLeftShift =
            this.sequenceBits + this.workerIdBits + this.dataCenterIdBits; // value：22
        this.sequenceMask = -1 ^ (-1 << this.sequenceBits); // value：4095
        this.lastTimestamp = -1;
        //set default value, from environmental variable
        this.workerId = 1;
        this.dataCenterId = 1;
        this.sequence = 0;
        if (this.workerId > this.maxWrokerId || this.workerId < 0) {
            throw new Error(
                "_workerId must max than 0 and small than maxWrokerId-[" +
                this.maxWrokerId +
                "]"
            );
        }
        if (this.dataCenterId > this.maxDataCenterId || this.dataCenterId < 0) {
            throw new Error(
                "_dataCenterId must max than 0 and small than maxDataCenterId-[" +
                this.maxDataCenterId +
                "]"
            );
        }

        this.workerId = _workerId;
        this.dataCenterId = _dataCenterId;
        this.sequence = _sequence;
    }
    Snowflake.prototype.tilNextMillis = function (lastTimestamp) {
        var timestamp = this.timeGen();
        while (timestamp <= lastTimestamp) {
            timestamp = this.timeGen();
        }
        return timestamp;
    };
    Snowflake.prototype.timeGen = function () {
        return Date.now();
    };
    Snowflake.prototype.nextId = function () {
        var timestamp = this.timeGen();
        if (timestamp < this.lastTimestamp) {
            throw new Error(
                "Clock moved backwards. Refusing to generate id for " +
                (this.lastTimestamp - timestamp)
            );
        }
        if (this.lastTimestamp === timestamp) {
            this.sequence = (this.sequence + 1) & this.sequenceMask;
            if (this.sequence === 0) {
                timestamp = this.tilNextMillis(this.lastTimestamp);
            }
        } else {
            this.sequence = 0;
        }
        this.lastTimestamp = timestamp;
        return (
            ((timestamp - this.twepoch) << this.timestampLeftShift) |
            (this.dataCenterId << this.dataCenterIdShift) |
            (this.workerId << this.workerIdShift) |
            this.sequence
        );
    };
    return Snowflake;
})();
let tempSnowflake = new Snowflake(1, 1, 0);
function PrefixInteger(num, length) {
    return (Array(length).join("0") + num).slice(-length);
}

function generateId() {
    let id = Math.abs(tempSnowflake.nextId());
    if (id.length < 11) {
        PrefixInteger(id, 11);
    }
    return String(id);
}

function stringToBoolean(str) {
    switch (str.toLowerCase()) {
        case "true": case "yes": case "1": return true;
        case "false": case "no": case "0": case null: return false;
        default: return Boolean(str);
    }
}

function emailValidVerify(email) {
    return new Promise((resolve, reject) => {

        let valid = tkUtils.isTestAccount(email)

        if (valid) {
            resolve(true)
            return
        } else {
            let url = "https://emailverification.whoisxmlapi.com/api/v1?apiKey=at_d0m69y8qyVEJ764fBFMIUGmUK4bP9&emailAddress=" + email
            axios.get(url)
                .then(res => {
                    valid = false
                    if (res.status) {
                        if (res.status === 200) {
                            let data = res.data
                            if (data) {
                                console.log('[Email校验] => 校验结果: ', JSON.stringify(data));
                                valid = stringToBoolean(data.smtpCheck)
                            }
                        }
                    }
                    resolve(valid)
                })
                .catch(err => {
                    console.error(err);
                    reject(err)
                })
        }
    })
}

function userId() {
    const userString = localStorage.getItem('auth_user')
    if (userString !== '') {
        let user = JSON.parse(userString)
        if (user) {
            return user.uid
        }
    }
    return ''
}

function objEquals(obj, target) {
    var props1 = Object.getOwnPropertyNames(obj);
    var props2 = Object.getOwnPropertyNames(target);
    if (props1.length != props2.length) {
        return false;
    }
    for (var i = 0, max = props1.length; i < max; i++) {
        var propName = props1[i];
        if (this[propName] !== obj[propName]) {
            return false;
        }
    }
    return true;
}

