function formatRepeatType(type) {
    switch (type) {
        // 不循环 : 0, weekly: 1, bi-weekly: 2, monthly: 3
        case 0:
            return 'No repeat';
        case 1:
            return 'Weekly';
        case 2:
            return 'Bi-weekly';
        case 3:
            return 'Monthly';
    }
}

function formatPrice(price) {
    let str = '$' + parseFloat(price).toFixed(2)
    return str
}

let commons = {
    emailValidVerify,
    userId,
    objEquals,
    userAvatarPath,
    studioLogoPath,
    formatPrice,
    formatRepeatType,
    generateId
}

export default commons