    /**
     * 根据 userId 获取 user
     * @param {*} userId 
     */
    getUserInfoByUserId(userId) {
        return new Promise(async (resolve, reject) => {
            try {
                let userDoc = await collections.user.doc(userId).get()
                if (userDoc.exists) {
                    resolve(userDoc.data())
                } else {
                    resolve(null)
                }
            } catch (error) {
                reject(error)
            }
        })
    },
