    /**
     * 根据 teacherId 获取 老师未来的课程数量
     * @param {*} teacherId 
     */
    getTeacherFutureLessonsCountByTeacherId(teacherId) {
        return new Promise(async (resolve, reject) => {
            try {
                let lessonsDoc = await collections.lessonSchedule
                    .where("teacherId", "==", teacherId)
                    .where('shouldDateTime', '>', parseInt(new Date().getTime() / 1000)).get()

                let lessons = []
                if (!lessonsDoc.empty) {
                    lessonsDoc.forEach(doc => {
                        if (doc.exists) {
                            lessons.push(doc.data())
                        }
                    })
                }

                resolve(lessons.length)

            } catch (error) {
                reject(error)
            }
        })
    },
    /**
     * 根据 teacherId 获取老师的所有 Lesson type
     * @param {*} teacherId 
     */
    async getLessonTypesByTeacherId(teacherId) {
        return new Promise(async (resolve, reject) => {
            try {
                let lessonTypesDoc = await collections.lessonType
                    .where("teacherId", "==", teacherId)
                    .get()

                let lessonTypes = []
                if (!lessonTypesDoc.empty) {
                    lessonTypesDoc.forEach(doc => {
                        if (doc.exists && !doc.data().deleted) {
                            let data = doc.data()
                            data.selected = false
                            tkUtils.instruments.some(item => {
                                if (parseInt(item.id) === parseInt(data.instrumentId)) {
                                    data.icon = item.url
                                }
                            })
                            data.desc = data.timeLength + "minutes, " + commons.formatPrice(data.price)
                            if (!data.deleted) {
                                lessonTypes.push(data)
                            }
                        }
                    })
                }

                resolve(lessonTypes)
            } catch (error) {
                reject(error)
            }
        })
    },
