    /**
     * 根据 lessonTypeId 和 lessonConfigId 获取该课程未上的课程(含有 package)
     * @param {*} lessonTypeId 
     * @param {*} lessonConfigId 
     */
    async getPackageLessonLeftById(lessonTypeId, lessonConfigId) {
        return new Promise(async (resolve, reject) => {
            try {
                let lessonsDoc = await collections.lessonSchedule.where("lessonTypeId", "==", lessonTypeId).where("lessonScheduleConfigId", "==", lessonConfigId).where("shouldDateTime", ">", parseInt(new Date().getTime() / 1000)).get()

                let lessons = []

                if (!lessonsDoc.empty) {
                    lessonsDoc.forEach(doc => {
                        if (doc.exists) {
                            lessons.push(doc.data())
                        }
                    })
                }

                resolve(lessons)
            } catch (error) {
                reject(error)
            }
        })
    },
    /**
     * 根据 lessonTypeId 和 lessonConfigId 获取该课程未上的课程节数(含有 package)
     * @param {*} lessonTypeId 
     * @param {*} lessonConfigId 
     */
    async getPackageLessonLeftCountById(lessonTypeId, lessonConfigId) {
        let count = await this.getPackageLessonLeftById(lessonTypeId, lessonConfigId)
        return count
    },
    /**
     * 根据 studioId 获取该 studio 的所有 lessonType
     * @param {*} studioId 
     * @returns 
     */
    listAllLessonTypesInStudio() {
        return new Promise(async (resolve, reject) => {
            try {
                let studioInfo = await userService.studioInfo()
                console.log('获取到的studioinfo: ', studioInfo)
                let studioId = studioInfo.id
                let teachers = await this.getTeachersByStudioId(studioId)
                console.log('获取到的教师信息: ', teachers)

                let lessonTypes = []
                for (let i = 0; i < teachers.length; i++) {
                    let lessonTypeDoc = await collections.lessonType.where("teacherId", "==", teachers[i].userId).get()
                    if (!lessonTypeDoc.empty) {
                        lessonTypeDoc.forEach(doc => {
                            if (doc.exists) {
                                let data = doc.data()
                                if (!data.deleted) {
                                    lessonTypes.push(data)
                                }
                            }
                        })
                    }
                }
                lessonTypes.sort((a, b) => {
                    return parseInt(a.createTime) - parseInt(b.createTime)
                })
                resolve(lessonTypes)
            } catch (error) {
                reject(error)
            }
        })
    },
